body{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.header{
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    height: 80px;
    width: 100%;
    
}

.logo>a>img{
    width: 80px;
    height: 80px;
    margin-left: 50px;
}

.nav-items{
    display: flex;
    align-items: center;
}

.nav-items>ul{
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    font-size: 18px;

}

.nav-items>ul>li{
    padding: 30px;
}